@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  height: 100vh;
  /* background-image: url("day.jpg"); */
  background-size: cover;
  background-position: center;
}

.header {
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header p {
  font-family: "Monoton", sans-serif;
  font-size: 4rem;
  font-weight: 300;
  color: #fc7341;
}

.buttonCon {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.btn {
  height: 2rem;
  width: 6rem;
  background-color: transparent;
  border-radius: 100px;
  cursor: pointer;
  border: none;
}

.day {
  background-color: #ffce99d5;
  color: #555;
}
.night {
  background-color: #555555e5;
  color: #ffce99;
}

.getWisedom {
  background-color: #fc7341;
  color: #78209a;
  font-size: 1.5rem;
  font-weight: 600;
  height: 6rem;
  width: 12rem;
  transition: all 0.5s;
}

.getWisedom:hover {
  background-color: #fd8255;
  box-shadow: 0 3px 5px 0 #cd66f5;
}

.adviceBox {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fcaa8c;
  border: 6px solid #fc7341;
  border-radius: 9px;
  height: 50vh;
  width: 80%;
  margin: 2rem auto;
  padding: 1rem 3rem;
}

.adviceBox p {
  color: #78209a;
}

.adviceBox p .wisdom {
  color: #a435d0;
  font-size: larger;
  font-weight: 600;
}
